$("._open-order").click(function () {
    let parent = $(this).parents(".gallery-slide")
    let title = $(parent).find(".gallery__title").html().trim()
    let imgSrc = $(parent).find(".gallery__img img").attr("src")

    $(".order-modal__title").html(title)
    $(".order-modal__img img").attr("src",imgSrc)
    $(".order-modal").addClass("_active")
    $("body").addClass("fixed-body")
})


$("._close-order").click(function () {
    $(".order-modal").removeClass("_active")
    $("body").removeClass("fixed-body")
})
